[data-accordion=element] .vacancies__details {
  display: none;
}

[data-accordion=element].is-active .vacancies__details {
  display: flex;
  max-height: fit-content;
}
[data-accordion=element].is-active .vacancy__button {
  transform: rotate(45deg);
}
[data-accordion=element].is-active .advantages__details {
  display: flex;
  max-height: fit-content;
}
[data-accordion=element].is-active .advantages__button {
  transform: rotate(45deg);
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s ease;
  overflow: hidden;
}

[data-tabs=parent] {
  overflow: hidden;
}
[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  position: relative;
  visibility: visible;
  opacity: 1;
}